.navigation {
	position: absolute;
	top: 25px;
	right: 10px;
	@include r(1024) {
		right: 20px; }
	@include r(720) {
		display: none; }
	&__list {
		display: inline; }
	&__item {
		display: inline-block;
		position: relative;
		margin-left: 62px;
		@include r(1024) {
			margin-left: 30px; } }
	&__link {
		color: white;
		font-family: Roboto;
		font-size: 18px;
		font-weight: 400;
		text-decoration: none;
		&:hover {
			text-decoration: underline; } } }

.logo {
	position: absolute;
	top: 26px; }
